.Contacts{
    height: 100vh;
    background: #f4bfc9;
}

.ContactsContainer{
    flex-direction: column;
    align-items: center;
}

.ContactsWrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    text-align: center;
}

.ContactsTitle{
    background: green;

}
.formContainer{
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    max-width: 300px;

}
.emailInput{
    margin-top: 20px;

}
.userNameInput{
    margin: 20px 0;

}
.buttonForm{
    margin-top: 20px;
}
.buttonForm>button{
    padding: 10px;
    width: 100px;
}

