.myProjects {
    min-height: 100vh;
    background: #ecb8ec;

}

.myProjectsContainer {
    align-items: center;
    flex-direction: column;

}
.projectsWrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
.projectsTitle{
    text-align: center;

}
.projects{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;


}

@media (max-height:776px){
    .projects{
        justify-content: center;
    }

}