.skillsBlock{
    min-height: 100vh;
    background: yellow;
}
.skillsContainer{
    flex-direction: column;

}
.skillsWrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
h2{
    background: pink;
}

.skills{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
    padding-bottom: 20px;

}

@media (max-height:776px){
    .skills{
        justify-content: center;
    }
    .skillsContainer{
        padding-bottom: 20px;
    }

}